.input-select {
	-webkit-appearance: none;
	appearance: none;
	border: none;
	width: 60px;
	margin-right: 100px;
	padding-left: 2px;
	background: url(arrow.png) no-repeat right white;
	background-size: 10px;

	font-size: 12px;
	font-weight: 400;
	line-height: 15px;
	letter-spacing: 0;
	text-align: left;

	&:focus {
		outline: none;
		box-shadow: none;
	}
}
